import * as React from 'react';
import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';
import Dropzone from 'react-dropzone';

import { DateHelper } from '../../shared/date';

export class ReportBox extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            file: null
        }
    }

    render() {
        const content = this.getContent();

        return (
            <Paper className={this.props.className}>
                {content}
            </Paper>
        );
    }

    getContent() {
        const {
            reportStatus,
            mark,
            dateOverdue,
            dateAccepted
        } = this.props;

        let content = null;

        if (reportStatus === 1 || reportStatus === 5) {
            content = this.getNotAcceptedContent();
        } else if (reportStatus === 2) {
            content = <p>  </p>;
        } else if (reportStatus === 3) {
            content = <p> </p>;
        } else if (reportStatus === 4) {
            content = this.getNotAcceptedContent();
        }

        return content;
    }

    getNotAcceptedContent() {
        const {
            reportStatus,
            dateOverdue
        } = this.props;

        let dateOverdueText = ' : ';
        dateOverdueText += dateOverdue
            ? DateHelper.ddmmyyyy(dateOverdue, '.')
            : '';

        let reportText = this.getReportText(reportStatus);
        
        return (
            <div>
                <p>{dateOverdueText}</p>
                <p>: <span>{reportText}</span></p>
                {this.getReportForm()}
            </div>
            );
    }

    getReportText(reportStatus) {
        let text = '';

        switch (reportStatus) {
            case 1:
                text = '   ';
                break;
            case 2:
                text = '  ';
                break;
            case 3:
                text = ' ';
                break;
            case 4:
                text = '  ';
                break;
            case 5:
                text = '  ';
                break;
            default:
                text = '';
                break;
        }

        return text;
    }

    getReportForm() {
        const file = this.state.file;

        let dropzoneContent = file
            ? <p> : {file.name}</p>
            : <p> </p>;

        const disabled = file == null;
        return (
            <div>
                <Dropzone
                    className="dropzone"
                    multiple={false}
                    onDrop={files => this.setState({ file: files[0] })}
                    accept=".docx,.doc,.pdf,.txt">
                    {dropzoneContent}
                </Dropzone>
                <RaisedButton
                    label=""
                    primary={true}
                    disabled={disabled}
                    style={{ marginTop: 30 }}
                    onClick={() => this.props.onSend(file)}
                />
            </div>
        );
    }
}


